@import url(https://fonts.googleapis.com/css2?family=Work+Sans:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  /* background-image: url("./img/sky.jpg"); */
  /* background-position: center;
  background-size: cover; */ /* hình full màn hình*/
  /* background-image: linear-gradient(180deg, #73C6B6, #F2F3F4); */
  background-color: #73C6B6;
}

::-webkit-scrollbar {
  width: 0px;
}

::-webkit-scrollbar-thumb {
  background: rgba(136, 136, 136, 0.281);
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}

.messages {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  overflow-y: scroll;
  scrollbar-width: none;
  /* background-image: linear-gradient(180deg, #AED6F1, white); */
  border-radius: 5px;
  width: 100%;
}
.singleChat{
  background-image: linear-gradient(180deg, white, #F2F3F4);
}
.chatOnlineFriend {
  display: block;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  font-weight: 500;
  cursor: pointer;
  margin-top: 10px;
  padding-left: 120px;
}

/* .chatOnlineImgContainer {
  position: relative;
  margin-right: 10px;
} */

.chatOnlineImg {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  object-fit: cover;
  border: 1px solid white;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.chatIcon {
  margin-top: 20px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  padding-left: 30px;
}

.chatText {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  padding-left: 30px;
  padding-right: 30px;
}

.chatOnlineBadge {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background-color: limegreen;
  position: absolute;
  top: 2px;
  right: 2px;
}

.emoji {
  position: absolute;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}
.svgQuocDang {
  margin-top: 10px;
  font-size: 2.0rem;
  color: gray;
  cursor: pointer;
  background-color: white;
  border-radius: 50%;
} 
.send{
  position: relative;
  -webkit-align-items: center;
          align-items: center;
  margin-left: 50px;

}

.micro{
  -webkit-align-items: center;
          align-items: center;
  margin-left: 10px;
}

.menu{
  background-color: blue;
    height:50px;
    width: 50px;
    position: absolute;
    left:-200px;
    bottom:0px;
}
